  <template> <!-- :style="[{height:clineHeight+'px'}]" -->
  	<view class="container" style="background-color: #ffffff;font-size: 18px;overflow: hidden;">
  		<uni-nav-bar title="实名认证" dark :fixed="true" shadow background-color="#007AFF" status-bar left-icon="left"
  			@clickLeft="back" />
  		<uni-section title="" type="line" style="margin-top: 30px;">
  			<uni-steps :options="list1" :active="active" />
  		</uni-section>
  		<view style="padding: 30px 30px 0px 30px;" class="view-form-css">
  			<view class="example-body custom-image-box col-view-css"
  				style="position: relative;height: 60px;line-height: 60px;">
  				<text class="text"><text style="color: red;">*</text>头像</text>
  				<uni-file-picker class="uni-file-picker-css" v-model="imageFile" limit="1" :del-icon="false"
				 @select="selectFileFun($event)"
  					disable-preview :imageStyles="imageStyles" file-mediatype="image"><uni-icons type="plusempty"
  						color="#cac4c4" size="30" /></uni-file-picker>
  			</view>
  			<view v-if="isHead" class="uni-form-item uni-column tips-view-css">
  				请选择头像
  			</view>
  			<view class="uni-form-item uni-column col-view-css">
  				<view class="title"><text style="color: red;">*</text>认证类型</view>
  				<view class="uni-list-cell-db view-input-css" style="line-height: 45px;">
  					<picker @change="bindPickerChange" :value="index" :range="array" range-key="name">
  						<view class="uni-input">{{array[index].name}}</view>
  					</picker>
  				</view>
  			</view>

  			<view v-if="isCertificationType" class="uni-form-item uni-column tips-view-css">
  				请选择认证类型
  			</view>
  			<!-- 	<view class="uni-form-item uni-column col-view-css">
  				<view class="title"><text style="color: red;">*</text>认证类型</view>
  				<input class="uni-input view-input-css" name="nickname" placeholder="请输入认证类型" />
  			</view> -->
  			<view class="uni-form-item uni-column col-view-css">
  				<view class="title" @click="sssss"><text style="color: red;">*</text>招聘城市</view>
  				<input class="uni-input view-input-css" v-model="city"  placeholder="请输入招聘城市" />
  			</view>
  			<view v-if="isCity" class="uni-form-item uni-column tips-view-css">
  				请选择招聘城市
  			</view>
  			<view class="uni-form-item uni-column col-view-css">
  				<view class="title"><text style="color: red;">*</text>姓名</view>
  				<input class="uni-input view-input-css" v-model="name" placeholder="请输入姓名" />
  			</view>
  			<view v-if="isName" class="uni-form-item uni-column tips-view-css">
  				请输入姓名
  			</view>

  			<view class="uni-form-item uni-column col-view-css">
  				<view class="title"><text style="color: red;">*</text>手机号</view>
  				<input class="uni-input view-input-css" v-model="phone" placeholder="请输入手机号" />
  			</view>
  			<view v-if="isPhone" class="uni-form-item uni-column tips-view-css">
  				请输入手机号
  			</view>

  			<view class="uni-form-item uni-column col-view-css">
  				<view class="title"><text style="color: red;">*</text>身份证号码</view>
  				<input class="uni-input view-input-css" v-model="idCard" placeholder="请输入身份证号码" />
  			</view>
  			<view  v-if="isID" class="uni-form-item uni-column tips-view-css">
  				请输入身份证号码
  			</view>
  			<view class="uni-form-item uni-column col-view-css">
  				<view v-if="isEnterprise" class="title"><text style="color: red;">*</text>公司名称</view>
  				<view v-if="isShop" class="title"><text style="color: red;">*</text>店铺名称</view>
  				<input v-if="isEnterprise" class="uni-input view-input-css" v-model="esName"  placeholder="请输入公司名称" />
  				<input v-if="isShop" class="uni-input view-input-css" v-model="esName" placeholder="请输入店铺名称" />
  			</view>
  			<view v-if="isEnterprise && isEnterpriseIf" class="uni-form-item uni-column tips-view-css">
  				请输入公司名称
  			</view>
  			<view v-if="isShop && isShopIf"  class="uni-form-item uni-column tips-view-css">
  				请输入店铺名称
  			</view>
  			<view class="uni-form-item uni-column col-view-css">
  				<view v-if="isEnterprise" class="title"><text style="color: red;">*</text>公司地址</view>
  				<view v-if="isShop" class="title"><text style="color: red;">*</text>店铺地址</view>
  				<input v-if="isEnterprise" class="uni-input view-input-css" v-model="esAdress" placeholder="请输入公司地址" />
  				<input v-if="isShop" class="uni-input view-input-css" v-model="esAdress" placeholder="请输入店铺地址" />
  			</view>
  			<view v-if="isEnterprise && isEnterpriseAddress" class="uni-form-item uni-column tips-view-css">
  				请输入工作地址
  			</view>
  			<view v-if="isShop && isShopAddress" class="uni-form-item uni-column tips-view-css">
  				请输入店铺地址
  			</view>
  			<view class="uni-form-item uni-column col-view-css">
  				<view v-if="isEnterprise" class="title"><text style="color: red;">*</text>公司简介</view>
  				<view v-if="isShop" class="title"><text style="color: red;">*</text>店铺简介</view>
  				<input v-if="isEnterprise" class="uni-input view-input-css" v-model="esSynopsis"  placeholder="请输入公司简介" />
  				<input v-if="isShop" class="uni-input view-input-css" v-model="esSynopsis"  placeholder="请输店铺简介" />
  			</view>
  			<view v-if="isEnterprise && isEnterpriseSynopsis" class="uni-form-item uni-column tips-view-css">
  				请输入公司简介
  			</view>
  			<view  v-if="isShop && isShopSynopsis" class="uni-form-item uni-column tips-view-css">
  				请输店铺简介
  			</view>
  			<view class="uni-form-item uni-column col-view-css">
  				<view class="title">邀请码</view>
  				<input class="uni-input view-input-css" v-model="invitationCode" placeholder="请输入邀请码" />
  			</view>
  			<view class="uni-btn-v" style="margin-top: 20px; margin-bottom: 20px;">
  				<button type="primary" @click="submit">下一步</button>
  			</view>
  			<!--   submit-->
  		</view>
  	</view>
  </template>

  <script>
  	export default {
  		data() {
  			return {
				city:'',
				name:'',
				phone:'',
				idCard:'',
				esName:'',
				esAdress:'',
				esSynopsis:'',
				invitationCode:'',
				isHead:false,
				isCertificationType:false,
				isCity:false,
				isName:false,
				isPhone:false,
				isID:false,
				isEnterpriseIf:false,
				isShopIf:false,
				isEnterpriseAddress:false,
				isShopAddress:false,
				isEnterpriseSynopsis:false,
				isShopSynopsis:false,
  				array: [{
  					name: '企业'
  				}, {
  					name: '个体工商户'
  				}, {
  					name: '工厂'
  				}, {
  					name: '家庭'
  				}, {
  					name: '微店'
  				}, {
  					name: '个人'
  				}],
  				index: 0,
  				isEnterprise: false,
  				isShop: false,
  				imageFile: [{
					"name":"1681557454375_0.jpg",
					        "extname":"jpg",
					        "url":"http://localhost:8080/c020f19b-c783-4e38-b28f-c761ec6c1854",
				}],
  				imageStyles: {
  					width: 64,
  					height: 64,
  					border: {
  						radius: '50%',
  						color: '#cac4c4',
  					}
  				},
  				clineHeight: 100,
  				active: 0,
  				list1: [{
  					title: '信息填写'
  				}, {
  					title: '资料上传'
  				}, {
  					title: '完成'
  				}],

  			};
  		},
  		computed: {

  		},
  		onLoad() {
  			this.getClineHeight();
  			this.setShowShopEnterprise();
  		},
  		watch: {},
  		methods: {
  			sssss() {
  				uni.navigateTo({
  					url: 'pages/attestation/address?data=Hello'
  				})
  			},


  			bindPickerChange: function(e) {
  				console.log('picker发送选择改变，携带值为：' + e.detail.value)
  				this.index = e.detail.value
  			},
  			setShowShopEnterprise() {
  				if (this.index === 0) {
  					this.isEnterprise = true
  					this.isShop = false
  				} else if (this.index === 1) {
  					this.isEnterprise = false
  					this.isShop = true

  				}

  			},
			selectFileFun(e) {
				const that = this
				console.log("eee",e)
						e.tempFilePaths.map(item => {
							console.log("item",item)
							that.imageFile.push({
								name: 'imageList',
								uri: item.uri
							})
						})
					},
  			submit() {
  				console.log(11)
  				console.log(this.imageFile)
				// isHead:false,
				if(this.imageFile.length == 0){
					uni.showModal({
						content: "请选择头像",
						confirmText: "确定",
						cancelText: "取消"
					})
					return;
				}
				if(this.city){
					
						uni.showModal({
							content: "请选择招聘城市",
							confirmText: "确定",
							cancelText: "取消"
						})
						return;
				}
				// city:'',
				// name:'',
				// phone:'',
				// idCard:'',
				// esName:'',
				// esAdress:'',
				// esSynopsis:'',
				// invitationCode:'',
				console.log(1111111111)
				// isCertificationType:false,
				// isCity:false,
				// isName:false,
				// isPhone:false,
				// isID:false,
				// isEnterpriseIf:false,
				// isShopIf:false,
				// isEnterpriseAddress:false,
				// isShopAddress:false,
				// isEnterpriseSynopsis:false,
				// isShopSynopsis:false,
  			},
  			change() {
  				if (this.active < this.list1.length - 1) {
  					this.active += 1
  				} else {
  					this.active = 0
  				}
  			},
  			getBarHeight() {
  				const res = uni.getSystemInfoSync()
  				if (res.platform === 'ios') {
  					return 44 + res.statusBarHeight
  				} else if (res.platform === 'android') {
  					return 48 + res.statusBarHeight
  				} else {
  					return 0;
  				}
  			},
  			//获取可视区域高度
  			getClineHeight() {
  				const res = uni.getSystemInfo({
  					success: (res => {
  						this.clineHeight = res.windowHeight - this.getBarHeight() + 500;
  						//this.clineHeight = res.windowHeight - this.getBarHeight() +821;
  					})
  				});
  			},
  		},
  	}
  </script>
  <style lang="scss" scoped>
  	.uni-file-picker-css {
  		position: absolute;
  		left: 500rpx;
  		top: 1rpx;
  	}

  	.container {
  		-webkit-overflow-scrolling: touch;
  		overflow: scroll;
  		height: 100%;
  	}

  	/* 去除滚动条样式 */
  	.container::-webkit-scrollbar {
  		width: 0;
  		background-color: transparent;
  	}

  	/deep/ ::-webkit-scrollbar {
  		width: 0;
  		background-color: transparent;
  	}

