  	// }
  </style>
  <style>
  	.tips-view-css {
  		color: red;
  		font-size: 15px;
  	}

  	/* 	   /deep/.uni-scroll-view::-webkit-scrollbar { display: none }
	   .uni-scroll-view::-webkit-scrollbar { display: none } */
  	.scroll-css::-webkit-scrollbar {
  		display: none;
  	}

  	/deep/::-webkit-scrollbar {
  		display: none;
  		width: 0;
  		height: 0;
  	}

